import React, { useState } from 'react';

import './layout.css';
import * as Yup from 'yup';

const Form = ( {AdderUser}) => {

    const [name,setName] = useState('');
    const [surname,setSurname] = useState('');
    const [age,setAge] = useState();
    const [location ,setLoction] = useState('');
    
    const submitUser = (e) =>
    {
        e.preventDefault();
        AdderUser(name,surname,age,location);
    }
return(
  <>
  <div className = "box">
  <div className = "login-box">
<h2>Register Student</h2>
<form onSubmit ={submitUser}>
<input type="text" value = {name} onChange = {(e) =>setName(e.target.value)} placeholder = "Enter your name" autoFocus />
<input type="text" value = {surname} onChange = {(e) =>setSurname(e.target.value)} placeholder = "Enter your surname" />
<input type="number" value = {age} onChange = {(e) =>setAge(e.target.value)} placeholder = "Enter your age"  />
<input type="text" value = {location} onChange = {(e) =>setLoction(e.target.value)} placeholder = "Enter your location"  />
<button type ="submit" > add user</button>
</form>
</div>
</div>
    </>
)
}

export default Form;