import React from 'react';
import './layout.css'
import { ErrorMessage , useField } from 'formik';

const RegisterForm = ({label , ...props}) =>{
 
const [field , meta] = useField(props)

       return(    
            <>
         
                   
               <label>{label}</label>
               <input
                {...field } {...props}
                autoComplete = "off">

               </input>
            <ErrorMessage name={field.name}></ErrorMessage>
             </>
         )

}
export default RegisterForm;