import React  from 'react';
import RegisterForm from './RegisterForm';
import { Formik ,Form} from 'formik';
import * as Yup from 'yup';
const Register = () => {
    const validate =Yup.object({
        name:Yup.string().required("Provide nme"),
        suraname:Yup.string().required("fill"),
        location :Yup.string().required("gjjjjj"),
        age:Yup.string().required("kkk")  

    })
   
    return(
        <>

        <Formik
         initialVaues={{
            name:'',
            suraname:'',
            location :'',
            age:''
    
         
        }}
        >
         {formik =>(
             <>
               <Form>
                <div className = "login-box">
                <h2>Register Student</h2>
                <RegisterForm label="Name"name = "name" type = "text"  placeholder = "Enter your name"/>
                <RegisterForm label="Surname"name = "surname" type = "text"  placeholder = "Enter your surname"/>
                <RegisterForm label="Location"name = "location" type = "text"  placeholder = "Enter your location"/>
                <RegisterForm label="Age"name = "age" type = "number"  placeholder = "Enter your age"/>
                <button type = "submit">get data</button>
                </div>
            </Form>
             </>
         )
}
        </Formik>
        </>
    )
}

export default Register;