import React, { useState } from 'react';
import Form from './Form';
import './layout.css';
import RegisterForm from './RegisterForm';
const Landing = () => {

    const [users, setUsers] = useState([]);

    const AdderUser = (name , surname , age , location) =>{
        setUsers([...users,{
            uid : users.length,
            _name :name,
            _surname : surname,
            _age : age,
            _location : location
        }]);
    }
    return(
        <>
    <div className = "box">
 <Form AdderUser = {AdderUser}/>
   <div className = "login-box1">
      
       {users.map(element => <p>{element._age}, {element._location}</p>)}
   </div>
   </div>
    </> 
    )
  
};

export default Landing;